
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import {httpUrl} from '../../http_url';

export default {
directives: {
TransferDom
},
data () {
return {
menus: {
menu1: '退出登录'
},
showMenus: false,
availableCredit:'0',
zhima_creditScore:'???',
realNameCredit:'0',
realNameStatus:'0',
realNameType:'未获取',
bankCredit:'0',
bankStatus:'0',
bankType:'未获取',
infoCredit:'0',
infoStatus:'0',
infoType:'未获取',
reportCredit:'0',
reporStatus:'0',
reportType:'未获取',
pengyuan_reportCredit:'0',
pengyuan_reporStatus:'0',
pengyuan_reportType:'未获取',
zhimaCredit:'0',
zhimaStatus:'0',
zhimaType:'未获取',
zmshow:false
}
},
created(){
this.getData();
},
components: {
XHeader,
PopupHeader,
Actionsheet,
Group,
Cell,
Grid,
GridItem

},
methods:{
getData(){
const _this = this;
this.$axios({
method: 'post',
url:httpUrl.creditInformation
}).then((res)=>{
console.log(res.data)
let data = res.data.retData;
if(res.data.errCode==200){
this.realNameCredit = data.realName.amount;
this.realNameStatus = data.realName.status;
this.realNameType = data.realName.status_msg;
this.bankCredit = data.bank.amount;
this.bankStatus = data.bank.status;
this.bankType = data.bank.status_msg;
this.infoCredit = data.relevantData.amount;
this.infoStatus = data.relevantData.status;
this.infoType = data.relevantData.status_msg;
this.pengyuan_reportCredit = data.pengyuan_credit.amount;
this.pengyuan_reporStatus = data.pengyuan_credit.status;
this.pengyuan_reportType = data.pengyuan_credit.status_msg;
this.zhimaCredit = data.zhima.amount;
this.zhimaStatus = data.zhima.status;
this.zhimaType = data.zhima.status_msg;
this.availableCredit = data.surplus_amount;

if(data.zhima_credit == '1'){
this.zhima_creditScore = '600分以下';
}else if(data.zhima_credit == '2'){
this.zhima_creditScore = '600分以上';
}else if(data.zhima_credit == '3'){
this.zhima_creditScore = '700分以上';
}

this.showPlugin();
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
showPlugin(){
if(this.realNameStatus==0){
const _this = this
this.$vux.confirm.show({
title:'提示',
content:"请先完成实名认证！",
confirmText:'实名认证',

onCancel () {

},
onConfirm () {
_this.$router.push('/certification');
}
})
}
},
openConfirm(){
if(this.realNameStatus == 0){
this.$vux.toast.show({
type:'warn',
text: "请先完成实名认证！"
})
}else {
if(this.zhimaStatus == 0){
this.zmshow = true
}else {
this.$vux.toast.show({
type:'warn',
text: '你已授权！'
})
}
}

},
openCredit(){
if(this.realNameStatus == 0){
this.$vux.toast.show({
type:'warn',
text: "请先完成实名认证！"
})
}else {
if(this.pengyuan_reporStatus == 0){
this.$router.push({
path:'/py_credit',
query: {
pengyuan_reporStatus:this.pengyuan_reporStatus
}
});
}else if(this.pengyuan_reporStatus == 1){
this.$router.push({
path:'/py_credit',
query: {
pengyuan_reporStatus:this.pengyuan_reporStatus
}
});
}
}
},
onZMCancel(){

},
onZMConfirm(){
const _this = this;
this.$vux.loading.show()
this.$axios({
method: 'post',
url:httpUrl.zhimaCredit
}).then((res)=>{
console.log(res.data)
this.$vux.loading.hide()
if(res.data.errCode==200){
this.$vux.toast.show({
type:'success',
text: '授权成功',
onHide () {
_this.getData();


}
})
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录'
})
}
});
},
goHome(){
this.$router.push('/home');
},
LoginOut(){
this.$axios({
method: 'post',
url:httpUrl.loginOut,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$router.push('/quick_login');
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}

}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
}
}
}



